      display: grid;
      grid-template-areas:
          "header  header  "
          "nav     nav     "
          "content content "
          "footer  footer  "
      ;
  
      grid-template-rows:100px 70px auto 70px;
  }

  .content{
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
  }

  .peli-item{
      width: 80%;
  }


  .nav a{
      margin-left: 0px;
      margin-right: 8px;
