  }


}


@media (max-width: 516px){

  .layout{
      width: 100%;
      height: auto;
  
      display: grid;
      grid-template-areas:
          "header  "
          "nav     "
          "content "
          "footer  "
      ;
  
      grid-template-rows:100px 70px auto 70px;
  }


  .content{
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
  }

  .peli-item{
      width: 80%;
  }


  .nav a{
      margin-left: 0px;
      margin-right: 0px;
      font-size: 15px;
  }

  .nav li {
      padding: 5px;
  }

}






